export default function timer () {
  // 
  var time_now = new Date()
  var year = time_now.getYear() + 1900 //
  var month = time_now.getMonth() + 1 //
  var day = time_now.getDate() //
  var hour = time_now.getHours() //
  var Min = time_now.getMinutes() //
  var s = time_now.getSeconds() //
  if (s < 10) {
    // 10 0
    s = '0' + s
  }
  var welcome = ''
  // 0-12 1218 
  if (hour >= 0 && hour < 12) {
    welcome = '' + welcome
  } else if (hour >= 12 && hour <= 18) {
    welcome = '' + welcome
  } else {
    welcome = '' + welcome
  }
  // 
  var YMDHms =
    '' +
    year +
    '' +
    month +
    '' +
    day +
    '' +
    '\xa0\xa0\xa0\xa0' +
    '' +
    hour +
    '' +
    Min +
    '' +
    s +
    '' +
    '\xa0\xa0\xa0\xa0' +
    welcome

  let YMD =
    '' +
    year +
    '' +
    month +
    '' +
    day +
    '' +
    '\xa0\xa0\xa0\xa0' +
    welcome
  // document.getElementById("time").innerHTML=print;	//print
  return { YMDHms, YMD }
}
